import http from '../../utils/http.js'
import api from '../../utils/api.js'
export default {
	data() {
		return {
			countFlag:false,
			btnnum:0,
			pageNo:1,
			pageSize:10,
			list:[],
			overFlag:false,
			scrollTop: 0,
			old: {
			    scrollTop: 0
			},
			count:0,
		}
	},
	onLoad() {
		this.getList()
	},
	methods: {
		// getList
		getList(){
			let self=this
			let param={
				type:self.btnnum,
				pageNo:self.pageNo,
				pageSize:self.pageSize,
				
			}
			http.Request(api.list,'POST',param,function(res){
				console.log(res)
				self.count=res.pages;
				  self.list.push(res.list[0]);
				if(res.list.length>0){
					for (var i=0;i<res.list.length;i++){
					    // self.list.push(res.list[0]);
					}
				}else{
					// 停止分页
					self.overFlag=true
				}
				
				// 数据判断
				if(res.pages>0){
					self.countFlag=false
				}else{
					self.countFlag=true
				}
				console.log(self.list)
			})
		},
	}
}
